<!-- 头部公用 -->
<template>
  <div class>
    <div class="headBack">
      <el-row>
        <!-- <el-col :span="4" :offset="4">wode </el-col>
        -->
        <el-col :span="12" :offset="4">
          <div class="headBox">
            <el-menu
              :default-active="activeIndex"
              class="el-menu-demo"
              mode="horizontal"
              @select="handleSelect"
              background-color="transparent"
              :router="true"
            >
              <!-- <el-menu-item class="is-active" index="/Home">>>首页</el-menu-item>
              <el-menu-item index="/Practice">>>练习</el-menu-item>
              <el-menu-item index="/Data">>>数据</el-menu-item>
              <el-menu-item index="/Aboutme">>>关于</el-menu-item>
              <el-menu-item index="/Aboutwebsite">>>建站</el-menu-item> -->
              <!-- 根据路由动态加载菜单 -->
              <el-menu-item
                v-for="(item,index) in $router.options.routes"
                :key="index"
                :index="item.path" 
                :class="$route.path==item.path?'is-active':''"
              >
              {{item.name}}
                <!-- <div v-if="index>0">{{item.name}}</div> -->
              </el-menu-item>
            </el-menu>
          </div>
        </el-col>
      </el-row>
    </div>

    <div class="jieshao">
      <p class="typer-dynamic">
        <span class="cut">
          I am&nbsp;
          <br />
          <span class="word" v-for="(letter,index) in words" :key="index">{{letter}}</span>
        </span>
        <!-- 模拟光标-->
        <!-- <span class="typer-cursor"></span> -->
      </p>
    </div>
    <div class="headImgBox" :style="{backgroundImage: 'url(static/img/header3.jpg)'}">
      <div class="bghtml">
        <iframe
          src="static/img/bjtx.html"
          frameborder="0"
          width="100%"
          scrolling="no"
          height="100%"
        ></iframe>
      </div>
      <div class="h-information">
        <div class="h-img">
          <a href="#/Aboutme">
            <img src="static/img/head.jpg" alt="头像" width="10%" />
          </a>
        </div>

        <h2 class="h-description">
          <!-- <a href="#/Aboutme">Write the Code. Change the World.</a> -->
          <span>今日事，今日毕</span>
        </h2>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Header",
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      words: [], //字母数组push，pop的载体
      str: "大橘为重", //str初始化
      letters: [], //str分解后的字母数组
      order: 1 //表示当前是第几句话
    };
  },

  methods: {
    //事件处理器
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    //开始输入的效果动画
    begin() {
      this.letters = this.str.split("");
      for (var i = 0; i < this.letters.length; i++) {
        setTimeout(this.write(i), i * 100);
      }
    },
    //开始删除的效果动画
    back() {
      let L = this.letters.length;
      for (var i = 0; i < L; i++) {
        setTimeout(this.wipe(i), i * 50);
      }
    },
    //输入字母
    write(i) {
      return () => {
        let L = this.letters.length;
        this.words.push(this.letters[i]);
        let that = this;
        /*如果输入完毕，在2s后开始删除*/
        if (i == L - 1) {
          setTimeout(function() {
            that.back();
          }, 2000);
        }
      };
    },
    //擦掉(删除)字母
    wipe(i) {
      return () => {
        this.words.pop(this.letters[i]);
        /*如果删除完毕，在300ms后开始输入*/
        if (this.words.length == 0) {
          this.order++;
          let that = this;
          setTimeout(function() {
            that.begin();
          }, 300);
        }
      };
    }
  },
  components: {
    //定义组件
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    order(old, newV) {
      if (this.order % 4 == 1) {
        this.str = "大橘为重";
      } else if (this.order % 4 == 2) {
        this.str = "looking for a job. ";
      } else if (this.order % 4 == 3) {
        this.str = "a front-end programmer.";
      } else {
        this.str = "coding the web...";
      }
    }
  },
  created() {
    //生命周期函数
    // console.log(this.$route);
  },
  mounted() {
    //页面元素加载完成
    this.begin();
  }
};
</script>

<style   >
/*********头部导航栏********/
/*头部导航栏盒子*/
.headBack {
  width: 100%;
  background-image: linear-gradient(
    to right,
    rgba(174, 173, 243, 0.5) 0%,
    rgba(106, 204, 166, 0.5) 50%,
    rgba(209, 130, 106, 0.5) 100%
  );
  /* background: #000; */
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;

  /* z-index 让其中一个div浮在另一个div上面 ,越大代表越置前*/
  /* margin-bottom:500px; */
}

.headBox .el-menu-demo {
  border-bottom: none !important;
}

.headBox li.el-menu-item {
  border-bottom: none !important;
  color: rgb(253, 253, 253);
}

.headBox .el-menu-item.is-active {
  border-bottom: none !important;
  color: rgb(20, 4, 245);
  font-size: 20px;
  font-weight: bold;
}
/* 自我介绍 */
.jieshao {
  top: 200px;
  position: relative;
  text-align: center;
  font-size: 40px;
  opacity: 0.7;
  height: 50px;

  width: 50%;
  transform: translate(50%, 0);
  /* opacity 透明度 */
  z-index: 9;
}

.typer {
  box-sizing: border-box;
}
.typer .typer-content {
  font-weight: bold;
  display: flex;
  flex-direction: row;
  letter-spacing: 4px;
}
.typer-dynamic {
  position: relative;
}
.cut {
  color: #000000;
}
.typer-cursor {
  position: absolute;
  height: 100%;
  width: 3px;
  top: 0;

  background-color: #0f0000;
  animation: flash 1.5s linear infinite;
}
/*头部背景图*/

.headImgBox {
  width: 100%;
  height: 800px;
  position: relative;
  background-size: cover;
  background-position: center 50%;
  background-repeat: no-repeat;
  transform: translate(0, -13%);
}
/* .h-information :hover {
  transform: translate(0, -2px);
} */
.h-information {
  text-align: center;
  width: 85%;
  margin: auto;
  height: 180px;
  position: relative;
  top: 550px;
  padding: 25px 0;
  font-size: 16px;
  opacity: 0.98;
  /* background: rgb(224, 211, 211); */
  border-radius: 13px;
  z-index: 1;

  animation: b 0.8s ease-out;
  -webkit-animation: b 0.8s ease-out;
}

@keyframes b {
  0% {
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
  }
  80% {
    -webkit-transform: translateY(2px);
    transform: translateY(2px);
  }
  90% {
    -webkit-transform: translateY(-2px);
    transform: translateY(-2px);
  }
  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.h-information img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  object-fit: cover;
}
.h-information img:hover {
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}

.bghtml {
  text-align: center;
  width: 50%;
  transform: translate(50%, 0);
  margin: auto;
  height: 200px;
  position: absolute;
  top: 530px;
  padding: 20px 0;
  font-size: 16px;
  opacity: 0.98;
  /* background: transparent; */
  /* background: rgb(231, 42, 42); */
  border-radius: 13px;
  z-index: 1;
}
</style>


