<!-- 底部公用 -->
<template>
  <div class="footBack">
    <div class="fcontainer">
      <p>
        博客已艰难运行
        <span v-html="longTime">{{longTime}}</span>
      </p>
      <p>
        托管于
        <a href="https://pluhuxc.github.io/2020/10/23/linuxdeploy-guide.html">Linux Deploy</a>.
        <a href="https://cn.vuejs.org/">Vue</a>.
        <a href="https://element.eleme.cn/#/zh-CN">Element</a> .
        <a href="https://www.ngrok.cc/">Ngrok</a>.
      </p>
      <p>
        © 2020 大橘为重. All Rights Reserved.
      
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    //选项 / 数据
    return {
      longTime: ""
    };
  },
  methods: {
    //事件处理器
    runTime: function() {
      //运行倒计时
      var that = this;
      var oldTime = new Date("2020/04/24 00:00:00");
      var timer = setInterval(function() {
        var nowTime = new Date();
        var longTime = nowTime - oldTime;
        var days = parseInt(longTime / 1000 / 60 / 60 / 24, 10); //计算剩余的天数
        var hours = parseInt((longTime / 1000 / 60 / 60) % 24, 10); //计算剩余的小时
        var minutes = parseInt((longTime / 1000 / 60) % 60, 10); //计算剩余的分钟
        var seconds = parseInt((longTime / 1000) % 60, 10); //计算剩余的秒数
        that.longTime =
          days + "天" + hours + "小时" + minutes + "分" + seconds + "秒";
      }, 1000);
    }
  },
  components: {
    //定义组件
  },
  created() {
    //生命周期函数
    //替换底部图片
    var that = this;
    that.runTime();
  }
};
</script>

<style>
.footBack {
  position: absolute;
  margin-bottom: 0px;
  line-height: 1.5;
  font-size: 12px;
  text-align: center;
  width: 100%;
}
.footBack .fcontainer {
  /* background-image: linear-gradient(
    to right,
    rgba(174, 173, 243, 0.5) 0%,
    rgba(106, 204, 166, 0.5) 50%,
    rgba(209, 130, 106, 0.5) 100%
  ); */
  width: 100%;
  padding: 15px 10px 10px 10px;
}
</style>


